* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: Helvetica;
  src: url("Helvetica-Bold.14246e03.ttf");
}

body {
  height: 100vh;
  width: 100vw;
  color: #1e1953;
  background-image: linear-gradient(to bottom right, #75c9b7, #7fcdb0, #8cd1a8, #9bd4a1, #abd699);
  padding: 2rem;
  font-family: Helvetica, sans-serif;
}

a {
  color: #ffe26a;
  text-decoration: none;
}

header {
  color: #fff;
}

header nav {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

header nav .droplist {
  display: relative;
}

header nav .droplist h3 {
  text-shadow: 4px 3px #7a7a7a;
  font-size: 3rem;
}

header nav .droplist .dropdown {
  text-align: right;
  width: 8rem;
  list-style: none;
  display: none;
  position: absolute;
  right: 10px;
}

header nav .droplist .dropdown a {
  padding: .5rem;
  font-size: 1.5rem;
  display: block;
}

header nav .droplist:hover .dropdown {
  display: block;
}

.text p {
  padding: 1rem 0 0;
}

.text p:nth-of-type(1) {
  font-size: 3rem;
}

.text p:nth-of-type(2) {
  font-size: 2rem;
}

.text p:nth-of-type(3) {
  font-size: 1.5rem;
}

form {
  width: 60%;
  max-width: 70%;
  margin: 0 auto;
}

form input:focus, form textarea:focus {
  outline-color: #ffe26a;
}

form .input {
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  gap: .5rem;
  display: flex;
}

form .input label {
  padding: 1rem 0 0;
  font-size: 1.5rem;
}

form .input input, form .input textarea {
  width: 100%;
  border: 1px solid #1e1953;
  border-radius: 1rem;
  padding: 1rem;
}

form button {
  color: #ffe26a;
  background-color: #1e1953;
  border: none;
  border-radius: 1rem;
  margin-top: 1rem;
  padding: 1rem 2rem;
  font-size: 1.1rem;
}

form button:hover {
  box-shadow: 0 0 32px 5px #000000b5;
}

